.expertny-soviet
    margin-top: 50px
    padding-bottom: 150px
    display: flex
    flex-direction: column
    gap: 70px
    +mq($max-width: 575px)
        margin-top: 30px
        padding-bottom: 100px
        gap: 50px
    &__group
        display: flex
        justify-content: space-between
        gap: 30px
        +mq($max-width: 575px)
            flex-direction: column
            gap: 20px
        h2
            font-family: 'Hagrid'
            font-style: normal
            font-weight: 500
            font-size: 31px
            line-height: 130%
            +mq($max-width: 575px)
                font-size: 17px
                text-align: center
    &__text
        display: flex
        flex-direction: column
        gap: 10px
    &__block
        display: flex
        flex-direction: column
        gap: 30px
        +mq($max-width: 575px)
            gap: 20px
    &__item
        max-width: 816px
        width: 100%
        background: #FFFFFF
        border: 1px solid #2D7ABF
        box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05)
        border-radius: 30px
        padding: 30px
        +mq($max-width: 575px)
            padding: 20px
            border-radius: 20px
        h3
            font-family: 'Hagrid'
            font-style: normal
            font-weight: 500
            font-size: 22px
            line-height: 130%
            margin-left: 182px
            +mq($max-width: 575px)
                margin-left: 0
                font-size: 14px
                text-align: center
        .flex
            gap: 30px
            margin-top: 20px
            +mq($max-width: 575px)
                flex-direction: column
                margin-top: 20px
                gap: 20px
            p
                font-weight: 600
                font-size: 18px
                line-height: 130%
                +mq($max-width: 575px)
                    font-size: 14px
            img
                max-width: 152px
                height: 152px
                border-radius: 30px
                +mq($max-width: 575px)
                    border-radius: 20px
                    max-width: 100%
                    height: calc( 100vw - 80px )
